	import interfaces from '../../utils/interfaces.js'
	export default{
		onLoad(option){
			// console.log(option);
			// 动态修改nav title
			uni.setNavigationBarTitle({
				title:option.name
			})
			// 加载数据
			this.loadData();
		},
		data(){
			return {
				goodsList: [], // 商品列表大数组
				filterby: "all", // 默认选择的tab
				page: 1,
				size: 6,
				loadingText: "正在加载....",
				filterByList: [{
							text: "全部",
							selected: true,
							filterby: "all" //过滤条件
						},
						{
							text: "口碑",
							selected: false,
							filterby: "public"
						},
						{
							text: "热门",
							selected: false,
							filterby: "hot"
						}
					],
			}			
		},
		methods:{
			// 页面跳转 商品详情
			handleGoods(goods){
				
				uni.navigateTo({
					url:'./goods?goodsInfo='+JSON.stringify(goods)
				})
			},
			handleSelect(index) {
				this.filterByList[index].selected = true;

				// 其他的selected false
				for (let i = 0; i < this.filterByList.length; i++) {
					if (i != index) {
						this.filterByList[i].selected = false;
					}
				}
				// 数据请求
				this.filterby = this.filterByList[index].filterby; // 切换条件
				this.page = 1;
				this.loadingText = "加载中...";
				this.goodsList = [];
				this.loadData();
		},
		loadData() {
				this.request({
					url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
					success: ((res) => {
						// console.log(res.data);
						if(res.data.length > 0){ // 如果上拉有数据则添加到列表数组里
							res.data.forEach(item => {
								this.goodsList.push(item);
								// console.log(this.goodsList);
							})
						}else{
							this.loadingText = "到底了";
						}
					})
				})
			},
			onPullDownRefresh(){
			setTimeout(() => {
				this.page = 1;
				this.loadingText = "加载中...";
				this.goodsList = [];
				this.loadData();
				uni.stopPullDownRefresh();
			},1000)
		},
		// 上拉加载
		onReachBottom(){
			this.page++;
			this.loadData();
		}
	},
}
